import React,{ Component } from 'react';
import { graphql } from 'react-apollo';
import { gql } from 'apollo-boost';
import { Row, Col } from 'react-bootstrap';
import { Typography, Button } from '@material-ui/core';
import { Link } from 'react-router-dom';
import { withRouter } from 'react-router';
import ReactCodeInput from 'react-verification-code-input';
import * as compose from 'lodash.flowright';

import './email-conf.style.css';
import Logo from '../../images/logo.png';


class  EmailConf extends  Component{
    constructor(props){
        super(props);
        console.log(this.props.location.state.from);
        this.state = {
            email : this.props.email,
            to : this.props.to,
            code : '',
            status : '',
            errorMessage : '',
            from : this.props.location.state.from
        }
    }
    render(){
        const handelSubmit = (e) => {
            e.preventDefault();
            let inputs = document.querySelectorAll('input');
            let code = "";
            for(var x = 0; x < inputs.length; x++){
                code = code + inputs[x].value
            }
            if(code.length === 5){
                this.setState({...this.state , code: code})                           
                this.props.mutate({
                    variables : {
                        email : this.state.email,
                        pin_code : code
                    }
                }).then((res)=>{
                    if(res.data.verifyPinCode.Status.message === "Pin Code Is Valid"){
                        this.props.history.push({
                            pathname: this.state.to,
                            state: { 
                                email: this.state.email,
                                code : this.state.code
                             }
                        });
                    }
                    this.setState({ ...this.state,status:res.data.verifyPinCode.Status.message })
                })
                .catch((err)=> this.setState({...this.state, errorMessage : "Email is not valid"}));
            }else{
                this.setState({ ...this.state,status: "please enter vaild pin code" })
            }}
     return (
            <div className="container pt-10">
                <form 
                    onSubmit={handelSubmit}
                    className="container mb-5" autoComplete="off">
                    <div className="pt-1 pb-3">
                        <img src={Logo} alt="spreago logo" width="150px"/>
                    </div>
                    <div className="pb-3">
                        <Typography variant="h4" color="primary" >Email Confirmation</Typography>
                    </div>
                    <div className="pt-3 pb-3">
                        <Typography variant="subtitle1" className="text-dark">Please write down th 5 digit that was sent to you on your email</Typography>
                    </div>
                    <Row className="text-center customInput">
                        <Col md={12}>
                          <ReactCodeInput 
                          fields={5} 
                          fieldHeight={100} 
                          className="w-auto" 
                          onComplete={()=>
                            {
                            let inputs = document.querySelectorAll('input');
                            let code = "";
                            for(var x = 0; x < inputs.length; x++){
                                code = code + inputs[x].value
                            }
                            if(code.length === 5){
                                this.setState({...this.state , code: code})                           
                                this.props.mutate({
                                    variables : {
                                        email : this.state.email,
                                        pin_code : code
                                    }
                                }).then((res)=>{
                                    if(res.data.verifyPinCode.Status.message === "Pin Code Is Valid"){
                                        this.props.history.push({
                                            pathname: this.state.to,
                                            state: { 
                                                email: this.state.email,
                                                code : this.state.code
                                            }
                                        });
                                    }
                                    this.setState({ ...this.state,status:res.data.verifyPinCode.Status.message })
                                })
                                .catch((err)=> this.setState({...this.state, errorMessage : "Email is not valid"}));
                            }}}
                          />
                        </Col>
                        <Col xs={12}>
                        <div className="text-danger"><p>{this.state.errorMessage}</p></div>
                        </Col>
                    </Row>
                    <Row className="pt-1">
                        <Col xs={12} className="text-center">
                            <Button size="large" type="submit" className="btn-orange-gradiant pr-5 pl-5" variant="contained" color="primary"><Typography>Next</Typography></Button>
                        </Col>
                        <Col xs={12} className="text-center pt-3">
                            <p className="text-orange">{this.state.status}</p>
                            <Typography variant="subtitle1" className="text-dark">Don't receive the mail? 
                            <Button 
                            size="large"
                            onClick={e => {
                                this.props.mutate({
                                    variables : {
                                        email : this.state.email,
                                    }
                                }).then((res)=>{
                                    if(res.data.sendPinCode.Status.message === "Pin Code Sended Successfully"){
                                        this.setState({...this.state, errorMessage : "Pin Code Sent Successfully"})
                                    }
                                })
                                .catch((err)=> this.setState({...this.state, errorMessage : "Email is not valid"}));
                                }} 
                             className="text-orange"> Resend now !</Button></Typography>
                        </Col>
                    </Row>

                </form>
            </div>
        );  
    }
}
const EMAIL_CONFIRMATION = gql`
mutation($email : Email ! $pin_code : String !){
    verifyPinCode(data: {
      email : $email
      pin_code : $pin_code
    }){
      Status{
        message
      }
      __typename
    }
  }
`;
const SEND_CODE = gql`
mutation( $email : Email! ){
    sendPinCode(email: $email){
      Status{
        message
      }
      __typename
    }
  }
`;

export default compose(graphql(EMAIL_CONFIRMATION),graphql(SEND_CODE))(withRouter(EmailConf));