import React, { Component } from 'react';
import { Row, Col } from 'react-bootstrap';
import { graphql } from 'react-apollo';
import IconButton from '@material-ui/core/IconButton';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import InputLabel from '@material-ui/core/InputLabel';
import InputAdornment from '@material-ui/core/InputAdornment';
import FormControl from '@material-ui/core/FormControl';
import TextField from '@material-ui/core/TextField';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import { FormControlLabel, Checkbox, Typography, Button, Fab } from '@material-ui/core';
import { FaFacebookF, FaGoogle } from 'react-icons/fa';
import { Link } from 'react-router-dom';
import { gql } from 'apollo-boost';
import { ValidatorForm} from 'react-material-ui-form-validator';
import { withRouter } from 'react-router';

import './login.style.css';
import Logo from '../../images/logo.png';



class  Login extends Component{
    constructor(props){
        super(props);
        this.state = {
            showPassword: false,
            checkedB: false,
            email : '',
            password : '',
            errorMessage : '',
            emailErrorMessage: '',
            user : '',
            code : '',
            bearerTokenResponse : '',
            to : ''
        }
    }
    componentWillMount(){
        if(localStorage.length !== 0){
            this.props.history.push({
                pathname:'/homepageApplicant',
                data : localStorage.userData
            });
                 
            }
        }
    render (){
        const handleChangePassword = prop => event => {
            this.setState({ ...this.state, [prop]: event.target.value });
        };
        const handleChangeEmail = prop => event => {
            this.setState({ ...this.state, [prop]: event.target.value });
        };
        const handleClickShowPassword = () => {
            this.setState({ ...this.state, showPassword: !this.state.showPassword });
        };
    
        const handleMouseDownPassword = event => {
            event.preventDefault();
        };
        const handleChangeC = name => event => {
            this.setState({ ...this.state, [name]: event.target.checked });
        };
        const CheckPassword = (inputtxt) =>
        {  
            var passw=  /(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}/;
            if(inputtxt.match(passw)) 
            {   
                this.setState({ ...this.state, errorMessage : '' });
                return true;
            }
            else
            { 
                this.setState({ ...this.state, errorMessage : 'The password must contains at least 8 characters that are uppercase and lowercase letters , special characters and numbers' });
                return false;
            }
        }
        const ValidateEmail = (mail) =>
        {
        if (/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(mail))
            {
                this.setState({ ...this.state, emailErrorMessage : ''});
                return (true)
            }
            this.setState({ ...this.state, emailErrorMessage : 'You have entered an invalid email address!' });
            return (false)
        }

    return (
            <div className="container">
                <ValidatorForm      
                    onSubmit={e => {
                    e.preventDefault();
                    ValidateEmail(this.state.email);
                    CheckPassword(this.state.password);
                    if(this.state.errorMessage === '' && this.state.emailErrorMessage === ''){
                        this.props.mutate({
                            variables : {
                                email : this.state.email,
                                password : this.state.password
                            }
                        }).then((res)=>
                        {     console.log(res)
                            if(res.data.login.user == null){
                                this.setState({...this.state, errorMessage : res.data.login.Status.message })
                            }
                            else {
                                this.setState({...this.state,errorMessage : res.data.login.Status.message, user :res.data.login.user ,code : res.data.login.Status.code , bearerTokenResponse :res.data.login.BearerTokenResponse })
                                this.props.history.push({
                                    pathname:'/homepageApplicant',
                                    data : res.data.login
                                });
                            }
                            if(this.state.checkedB === true){
                                localStorage.setItem('userData',res.data.login);
                                }
                        } )
                        .catch((err)=>
                        this.setState({...this.state, errorMessage : "Email or Password is not valid"})
                        );                    
                    }
                    }}
                     className="container pt-10 pb-1" autoComplete="off">
                    <div className="pt-1 pb-3">
                        <img src={Logo} alt="spreago logo" width="150px"/>
                    </div>
                    <div className="pb-3">
                        <Typography variant="h4" color="primary">Welcome Back</Typography>
                    </div>
                    {/* <div className="pt-1 pb-3">
                        <Typography variant="subtitle1" className="text-dark">We make it easy for everyone to invest in real state</Typography>
                    </div> */}
                    <TextField
                        required
                        fullWidth
                        label="Email"
                        type ="email"
                        color="primary"
                        variant="outlined"
                        value={this.state.email}
                        onChange={handleChangeEmail('email')}
                    />
                    <FormControl required fullWidth className="mt-3" variant="outlined">
                        <InputLabel htmlFor="outlined-adornment-password">Password</InputLabel>
                        <OutlinedInput
                            id="outlined-adornment-password"
                            type={this.state.showPassword ? 'text' : 'password'}
                            value={this.state.password}
                            onChange={handleChangePassword('password')}
                            endAdornment={
                                <InputAdornment position="end">
                                    <IconButton
                                        aria-label="toggle password visibility"
                                        onClick={handleClickShowPassword}
                                        onMouseDown={handleMouseDownPassword}
                                        edge="end"
                                    >
                                        {this.state.showPassword ? <Visibility /> : <VisibilityOff />}
                                    </IconButton>
                                </InputAdornment>
                            }
                            labelWidth={70}
                        />
                    </FormControl>
                    <Row>
                        <Col xs={12}>
                           <div className="text-danger"><p>{this.state.errorMessage}</p></div>
                        </Col>
                        <Col xs={12}>
                           <div className="text-danger"><p>{this.state.emailErrorMessage}</p></div>
                        </Col>
                        <Col lg={4} xs={6}>
                            <FormControlLabel
                                className="d-inline-block"
                                control={
                                    <Checkbox
                                        checked={this.state.checkedB}
                                        onChange={handleChangeC('checkedB')}
                                        value="checkedB"
                                        label="Remember me"
                                        color="primary"
                                    />
                                }
                                label="Remember me"
                            />
                        </Col>
                        <Col lg={{ span: 4, offset: 4 }} xs={6}>
                            <Typography  className="d-inline-block mt-1 pt-1">
                                <Link to='/forgetPassword'  className="text-orange">
                                     Forgot Password?
                                </Link>
                            </Typography>
                        </Col>
                        <Col xs={6} className="text-right">
                            <Button type="submit" size="large" className="btn-orange-gradiant pr-5 pl-5" variant="contained" to={this.state.to} >Login</Button>
                        </Col>
                        <Col xs={6} className="text-left">
                            <Button size="large" className="btn-white-gradiant pr-5 pl-5"  variant="contained" 
                            onClick={e=>{
                                    this.props.history.push({
                                        pathname:'/createAccount'
                                    });
                            }} color="secondary" >Register</Button>
                        </Col>
                        <Col md={12}>
                             <h6 className="dividor-container pt-3 pb-1"><span>OR</span></h6>
                        </Col>
                        {/* <Col className="text-right">
                            <Fab size="medium" variant="outlined"  className="btn-social bg-white" aria-label="add">
                                <FaGoogle size="1.5em" />
                            </Fab>
                        </Col>
                        <Col className="text-left">
                            <Fab size="medium" variant="outlined" className="btn-social bg-white" aria-label="add">
                                <FaFacebookF size="1.5em" />
                            </Fab>
                        </Col> */}
                        <Col className="text-center">
                            <div  className="btn-fb box-shadow-none text-center pt-1 rounded pb-1">
                                <div className="fb-login-button text-center" data-width="" data-size="medium" data-button-type="login_with" data-auto-logout-link="false" data-use-continue-as="false"></div>
                            </div>
                        </Col>
                        <Col className="text-center">
                            <div  className="bg-white btn-google text-center rounded">
                                  <div className="g-signin2 text-center" data-onsuccess="onSignIn"></div>
                            </div>
                        </Col>
                    </Row>
                        
                </ValidatorForm>
            </div>
    );
    }
   
}
const LOGIN = gql`
mutation( $email : Email ! $password : String ! ){
   login(data: {
     attempt:{
       email: $email
       password: $password 
     }
   }){
     BearerTokenResponse{
       access_token
       token_type
     }
     user{
       name
       
     }
     Status{
       message
       code
       check
       title
     }
   }
 }
`;
export default graphql(LOGIN)(withRouter(Login));